.WrapperRegisterForm{position:relative;width:100%;height:100vh}.RegisterMain{display:flex;justify-content:space-between;position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);width:800px}.RegisterImg img{width:100%;height:100%;object-fit:cover}.RegisterForm{color:#0095f3;width:250px}.RegisterFormMain{margin-top:27px}.FormInput{width:100%;height:30px;border:1px solid #0095f3;border-radius:4px;padding-left:10px;margin-bottom:10px}.FormInput input{outline:unset;border:rgba(0,0,0,0);background-color:rgba(0,0,0,0);padding-left:0;color:#0095f3;font-weight:700}.FormSelect{width:100%;height:30px;color:#0095f3;border:1px solid #0095f3;background-color:rgba(0,0,0,0);outline:none;margin-bottom:10px;border-radius:4px}.CheckBoxItem input{-moz-appearance:none;-webkit-appearance:none;-o-appearance:none;outline:none;content:none}.CheckBoxItem input:before{font-family:"FontAwesome";content:"âœ“";font-size:11px;color:rgba(0,0,0,0) !important;background:rgba(0,0,0,0);display:block;width:12px;height:12px;border:1px solid #0095f3;margin-right:7px;font-weight:bold}.CheckBoxItem input:checked:before{color:#000 !important}.CheckBoxSubmit{width:100%;text-align:center}.CheckBoxSubmit input{width:100px;height:30px;background-color:rgba(0,0,0,0);color:#0095f3;border:1px solid #0095f3;border-radius:4px;margin-top:10px;cursor:pointer}.CheckBoxSubmit input:hover{background-color:#2699fb;color:#fff}@media(max-width: 767px){.RegisterImg{display:none}.RegisterMain{width:100%;justify-content:center}}